      </template>
    </modal>

    <modal
      :show.sync="showEditTeacher"
      class="tk-popup-outer"
      style="z-index: 2020"
    >
      <template slot="header">
        <h5 class="modal-title">Edit Teacher</h5>
      </template>
      <div class="tk-popup-content">
        <form class="card-wrapper">
          <span>
            <div class="form-group card-wrapper tk-px-margin-bottom-px0">
              <label class="form-control-label">Full Name</label>
              <div class="card tk-px-margin-bottom-px16">
                <div class="has-label card-body tk-px-padding-px0">
                  <input
                    type="text"
