    <modal :show.sync="showDelete" style="z-index: 2020">
      <template slot="header">
        <h5 class="modal-title">Delete Teacher</h5>
      </template>
      <div class="tk-popup-content">
        Do you want to delete the teacher
        <strong class="tk-font-color-black1">[{{ fullName }}]</strong>
        ?
      </div>
      <template slot="footer">
        <base-button
          type="secondary"
          @click.prevent="showDelete = false"
          class="tk-font-color-lightGray tk-border-none"
          >Close</base-button
        >
        <base-button
          type="primary"
          @click.prevent="confirmDelete"
          class="tk-font-color-white tk-bg-color-red tk-border-none"
          >Delete</base-button
        >
